<template>
    <div class="detail-topic">
        <div class="topic-sender">
            <img class="sender-head-img" :src="topic.headImage" alt="">
            <div class="sender-nickname-date">
                <div class="nickname">{{topic.nickname}}</div>
                <div class="topic-date">{{topic.sendTime}}</div>
            </div>
            <button class="btn-follow"></button>
        </div>
        <div class="topic-text">{{topic.text}}</div>
        <ul class="topic-images">
            <template v-if="topic.images.length === 1">
                <li @click="previewImage(index)" class="image-item image-item1" v-for="(item,index) in topic.images"
                    :key="index"
                    :style="{background: 'url('+item+')center no-repeat',backgroundSize: 'cover'}"></li>
            </template>
            <template v-if="(topic.images.length === 2)||(topic.images.length === 4)">
                <li @click="previewImage(index)" class="image-item image-item2" v-for="(item,index) in topic.images"
                    :key="index"
                    :style="{background: 'url('+item+')center no-repeat',backgroundSize: 'cover'}"></li>
            </template>
            <template v-if="(topic.images.length > 4)||(topic.images.length === 3)">
                <li @click="previewImage(index)" class="image-item image-item3" v-for="(item,index) in topic.images"
                    :key="index"
                    :style="{background: 'url('+item+')center no-repeat',backgroundSize: 'cover'}"></li>
            </template>
        </ul>
    </div>
</template>

<script>
  import {ImagePreview} from 'vant'

  export default {
    name: "myFeedback",
    data() {
      return {
        tabFixed: false,
        topic: {
          headImage: 'https://img.yzcdn.cn/public_files/2017/09/05/3bd347e44233a868c99cf0fe560232be.jpg',
          nickname: '周杰伦',
          sendTime: '2018年01月01日',
          text: '我需要冷静一下了，我太优秀，简直是一件很nice的事情哦，biubiu，哒哒哒',
          images: [
            // 'https://img.yzcdn.cn/public_files/2017/09/05/3bd347e44233a868c99cf0fe560232be.jpg',
            // 'https://img.yzcdn.cn/public_files/2017/09/05/c0dab461920687911536621b345a0bc9.jpg',
            'https://img.yzcdn.cn/public_files/2017/09/05/4e3ea0898b1c2c416eec8c11c5360833.jpg',
            'https://img.yzcdn.cn/public_files/2017/09/05/fd08f07665ed67d50e11b32a21ce0682.jpg',
            'https://img.yzcdn.cn/public_files/2017/09/05/4e3ea0898b1c2c416eec8c11c5360833.jpg'
          ],
          videoUrl: 'https://www.betterman.top/videos/superfestival.mp4'
        }
      }
    },
    mounted() {
      window.addEventListener('scroll', this.scrollTopDis);
    },
    methods: {
      scrollTopDis: function () {
        let winTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let elTop = document.querySelector('.topic-comment').offsetTop;
        if (winTop > elTop) {
          this.tabFixed = true;
        } else {
          this.tabFixed = false;
        }
      },
      previewImage: function (e) {
        ImagePreview({
          images: this.topic.images,
          startPosition: e
        })
      },
    },
    destroyed() {
      window.removeEventListener('scroll', this.scrollTopDis)
    }
  }
</script>

<style scoped lang="scss">
    .detail-topic {
        width: 100%;
        height: 100vh;
        background: #fff;
        .topic-sender {
            height: .71rem;
            width: 100%;
            padding: 0 .24rem;
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            .sender-head-img {
                flex: 0 0 auto;
                display: block;
                height: .71rem;
                width: .71rem;
                border-radius: 50%;
            }
            .sender-nickname-date {
                flex: 0 0 auto;
                height: .58rem;
                margin: .065rem 0;
                width: 70%;
                .nickname,
                .topic-date {
                    width: 100%;
                    font-size: .34rem;
                    color: #333333;
                    overflow: hidden;
                }
                .nickname {
                    height: 55%;
                }
                .topic-date {
                    height: 45%;
                    font-size: .22rem;
                    color: #909090;
                    line-height: .35rem;
                }
            }
            .btn-follow {
                height: .58rem;
                line-height: .58rem;
                width: 1.16rem;
                -webkit-border-radius: .06rem;
                -moz-border-radius: .06rem;
                border-radius: .06rem;
                border: .02rem solid #FF2E4D;
                color: #FF2E4D;
                margin: .065rem 0;
                font-size: .26rem;
                background: #fff;
                overflow: hidden;
                visibility: hidden;
            }
        }
        .topic-text {
            width: 100%;
            padding: .32rem .24rem .25rem .24rem;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            color: #333333;
            font-size: .28rem;
            line-height: .42rem;
            font-weight: bold;
        }
        .topic-images {
            display: flex;
            justify-content: flex-end;
            width: 100%;
            padding: 0 .24rem;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            flex-wrap: wrap;
            .player {
                width: 100%;
                height: 4.61rem;
                .video {
                    width: 100%;
                    height: 100%;
                    object-position: center top;
                    object-fit: contain;
                }
            }
            .image-item {
                flex: 0 0 auto;
                img {
                    display: block;
                    height: 100%;
                    width: 100%;
                }
            }
            .image-item1 {
                width: 4.61rem;
                height: 5.71rem;
            }
            .image-item2 {
                width: 3.46rem;
                height: 3.46rem;
                margin-bottom: .09rem;
                &:nth-of-type(2n) {
                    margin: 0 0 .09rem .08rem;
                }
            }
            .image-item3 {
                width: 2.28rem;
                height: 2.28rem;
                margin-bottom: .09rem;
                &:nth-of-type(2n) {
                    margin: 0 .08rem .09rem .08rem;
                }
            }
        }
        .btn-prise {
            display: block;
            height: .56rem;
            line-height: .56rem;
            width: 1.2rem;
            border: .02rem solid #FF2E4D;
            color: #FF2E4D;
            border-radius: .28rem;
            margin: .48rem auto;
        }
        .related-recommend {
            width: 100%;
            padding: 0 .24rem;
            border-top: .08rem solid #F3F3F3;
            border-bottom: .08rem solid #F3F3F3;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            h3 {
                font-weight: 900;
                color: #000;
                font-size: .34rem;
                margin-top: .32rem;
            }
            .related-content {
                width: 100%;
                overflow: hidden;
                padding: .32rem 0;
                .related-img {
                    display: block;
                    height: 1.63rem;
                    width: 1.63rem;
                    float: left;
                    border-radius: .1rem;
                }
                .related-describe {
                    width: 70%;
                    height: 100%;
                    float: left;
                    padding: 0 .2rem;
                    -webkit-box-sizing: border-box;
                    -moz-box-sizing: border-box;
                    box-sizing: border-box;
                    .describe-text {
                        height: .85rem;
                        width: 100%;
                        color: #333;
                        font-size: .28rem;
                        line-height: .42rem;
                        overflow: hidden;
                        -ms-text-overflow: ellipsis;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 2
                    }
                    .related-source {
                        color: #909090;
                        font-size: .24rem;
                        margin-top: .4rem;
                    }
                }
            }
        }
    }

    .fixedComment {
        position: fixed;
        left: 0;
        top: 0;
        padding: 0 .24rem;
        z-index: 2;
    }

</style>

